<template>
  <div class="history_practise">
    <head-top title="随堂练习"></head-top>
    <div class="main">
      <div class="content">
        <div
          class="tt"
        >{{currentIndex + '.【' + getPracticeTypeName(practiceInfo['PSType']) + '】' + (practiceInfo['PSScore']||0) + '分'}}</div>

        <div class="ct">{{practiceInfo['PSContent']}}</div>
        <div v-if="practiceInfo['PSType']===1 || practiceInfo['PSType']===2 " class="ans">
          <div class="an" v-for="(item, k) in practiceOptions" :key="k">
            <span>{{ item['SOSerial']}}：</span>
            <span>{{item['SOContent']}}</span>
          </div>
        </div>
      </div>

      <my-answer
        :question-type="practiceInfo['PSType']"
        :my-answer="practiceAnswer"
        :answer-type="contentType"
        :module-picture="modulePicture"
        :true-answer="practiceInfo['PSAnswer']"
        :analysis="practiceInfo['PSanalysis']"
        :get-score="practiceReply? practiceReply['PAScore'] : 0"
      ></my-answer>

      <div class="survey_info">
        <van-button icon="success" type="primary" disabled v-if="surveyMaster===1">已掌握</van-button>
        <van-button icon="cross" type="danger" disabled v-else-if="surveyMaster===0">未掌握</van-button>

        <van-button type="warning" disabled v-else>未参与调研</van-button>
      </div>

      <div class="footer">
        <button class="backTo" v-show="currentIndex> 1" @click="backTo">上一题</button>
        <button class="nextTo" v-if="currentIndex< total" @click="nextTo">下一题</button>
      </div>
    </div>
  </div>
</template>

<script>
import { Button } from "vant";
import headTop from "@/components/headTop";
import MyAnswer from "./../../course/quiz/components/MyAnswer";

import { practiceTypeName, ContentType, ModulePicture } from "@/utils/enum";
export default {
  name: "",
  components: { headTop, MyAnswer, [Button.name]: Button },
  props: {},
  data() {
    return {
      fromID: this.$route.params.id,
      practiceInfo: {},
      practiceOptions: [],
      practiceReply: {},
      total: parseInt(this.$route.params.total || "1"),
      practiceAnswer: "",
      contentType: ContentType.Text, //1文本 2图片
      contentTypeDefault: ContentType.Text,
      modulePicture: ModulePicture.PractiseAnswer,
      surveyMaster: null,
      currentIndex: 1
    };
  },
  created() {
    this.getDataInfo();
  },
  methods: {
    getDataInfo() {
      Object.keys(this.practiceInfo).forEach(item => {
        this.$delete(this.practiceInfo, item);
      });
      Object.keys(this.practiceReply).forEach(item => {
        this.$delete(this.practiceReply, item);
      });
      this.practiceOptions = [];
      this.contentType = this.contentTypeDefault;

      this.$mainHttp.classPractise
        .getData({
          cpID: this.fromID,
          psSerial: this.currentIndex,
          studentID: this.$store.getters.studentID
        })
        .then(res => {
          if (res.Result) {
            this.practiceInfo = res.PSData;
            this.practiceReply = res.PAData ? res.PAData : {};
            this.practiceAnswer = this.practiceReply["PAContent"];

            if (this.practiceReply) {
              this.surveyMaster = this.practiceReply["PAMaster"] || null;
            }
            this.contentType =
              this.practiceReply["PAForm"] || this.contentTypeDefault;

            this.practiceOptions = [];
            if (res.OSData && res.OSData.length > 0) {
              res.OSData.forEach(item => {
                if (this.practiceOptions.indexOf(item) === -1)
                  this.practiceOptions.push(item);
              });
            }
          }
        })
        .catch(err => {});
    },
    getPracticeTypeName(val) {
      return practiceTypeName(val);
    },
    nextTo() {
      this.currentIndex += 1;
      this.getDataInfo();
    },
    backTo() {
      this.currentIndex -= 1;
      this.getDataInfo();
    },
    exit() {
      this.$router.push("/history");
    }
  }
};
</script>

<style lang='less' scoped>
.history_practise {
  width: 100%;
  height: 100%;
  background: #ebf0ef;
  .main {
    text-align: left;
      background: #ebf0ef;
    .content {
      width: 92vw;
      background: rgba(255, 255, 255, 1);
      padding: 4vw;
      color: #333333;
      font-size: 1.4rem;
      margin-bottom: 2vh;
      word-break: break-all;
      .tt {
        margin-bottom: 15px;
      }
      .ct {
        text-indent: 2em;
        margin-bottom: 15px;
      }
      .ans {
        padding-left: 10px;
        .an {
          display: flex;
          margin-bottom: 10px;
        }
      }
    }

    .footer {
      display: flex;
      justify-content: space-around;
      padding: 2rem 0;
      button {
        background: rgba(0, 203, 171, 1);
        border-radius: 5px;
        font-size: 1.3rem;
        line-height: 3;
        color: #fff;
        padding: 0 1.5rem;
      }
    }
  }
}
.survey_info {
  display: flex;
  justify-content: center;

  width: 100vw;
  padding: 2vh 0;
  background: #ffffff;
  button {
    width: 50%;
    margin: 0 10vw;
  }
}
</style>


